@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap");
*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
}

body {
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  color: #fff;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --bg-primary-cyan: hsl(185, 75%, 39%);
  --dark-blue: hsl(229, 23%, 23%);
  --dark-grayish-blue: hsl(227, 10%, 46%);
  --dark-gray: hsl(0, 0%, 59%);
  --gray: hsl(0, 1%, 78%);
  --light-gray: #eee;
}

.profiles {
  height: 100vh;
  background-color: var(--bg-primary-cyan);
  background-image: url(/images/bg-pattern-top.svg), url(/images/bg-pattern-bottom.svg);
  background-position: top -43rem left -1rem, bottom -50rem right -2.4rem;
  background-repeat: no-repeat;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 41.6875em) {
  .profiles {
    background-size: 50rem;
    background-position: top -23rem left -28rem, bottom -21rem right -24rem;
  }
}
@media (max-width: 23.4375em) {
  .profiles {
    background-size: 50rem;
    background-position: top -26rem left -29rem, bottom -20rem right -31rem;
  }
}

.profile-card {
  background-color: #fff;
  border-radius: 1.6rem;
  cursor: pointer;
  overflow: hidden;
  box-shadow: 0 2.4rem 4.8rem rgba(0, 0, 0, 0.15);
}
@media (max-width: 41.6875em) {
  .profile-card {
    width: 30rem;
  }
}
@media (max-width: 23.4375em) {
  .profile-card {
    width: 33rem;
  }
}
.profile-card__hero {
  max-width: 100%;
  border-radius: 1.6rem 1.6rem 0 0;
}
.profile-card__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.profile-card__info {
  line-height: 1.5;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 0.4rem;
}
.profile-card__img {
  height: 10.6rem;
  width: 10.6rem;
  object-fit: cover;
  border: 5px solid #fff;
  border-radius: 100%;
  transform: translateY(-50%);
  margin-bottom: -30%;
}
.profile-card__person {
  font-size: 1.8rem;
  font-weight: 700;
  color: var(--dark-blue);
}
.profile-card__years {
  font-weight: 400;
  color: var(--dark-grayish-blue);
  margin-left: 0.8rem;
}
.profile-card__city {
  font-size: 1.4rem;
  font-weight: 500;
  color: var(--dark-gray);
}
.profile-card__stats-box {
  width: 100%;
  border-top: 1px solid var(--light-gray);
  display: flex;
  justify-content: space-around;
  margin-top: 2.4rem;
}
.profile-card__stat {
  padding: 2.4rem;
  text-align: center;
}
.profile-card__stat:first-of-type {
  margin-left: 2.4rem;
}
.profile-card__stat:last-of-type {
  margin-right: 2.4rem;
}
.profile-card__stat-value {
  font-size: 1.8rem;
  color: var(--dark-blue);
  font-weight: 700;
  margin-bottom: 0.4rem;
}
.profile-card__stat-value span {
  text-transform: uppercase;
}
.profile-card__stat-name {
  font-size: 1.2rem;
  color: var(--dark-gray);
  letter-spacing: 1.2px;
}